@use '../../assets/styles/utils.scss' as *;

.hero_container {
  position: relative;
  background: url('../../assets/images/home-hero-background.jpg');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  padding: 5rem 0 10rem;

  @include breakpoint(desktop, md) {
    background-position: top center;
  }

  @include breakpoint(desktop, sm) {
    background-size: auto 100%;
  }

  @include breakpoint(mobile, sm) {
    background-position: 100% 0;
  }

  // border svg
  >svg {
    position: absolute;
    bottom: -1px;
    width: 100%;
  }

  .hero_inner {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 0;
    // padding: 6.5rem 0 10rem;
    width: min(410px, 90%);

    // background-color: rgba(#fff, 0.8);
    // box-shadow:
    //   0px 0px 10px 10px rgba(#fff, 0.7),
    //   0px 0px 10px 20px rgba(#fff, 0.5);
    //   // 0px -50px 10px 50px rgba(#fff, 0.5),
    //   // 0px -50px 10px 70px rgba(#fff, 0.5),
    //   // 0px -50px 10px 90px rgba(#fff, 0.5);

    @include breakpoint(mobile, sm) {
      margin: auto;
      text-align: center;
      background-color: rgba(#fff, 0.9);
      padding: 2rem 3rem; 
      width: 100%;
      border-radius: 1rem;
    }

    h1 {
      color: var(--c-blue-accent-400);
      font-family: var(--ff-primary);
      font-size: var(--fs-900);
      font-weight: 800;
      line-height: 1;
    }

    p {
      color: var(--c-neutral-800);
      font-family: var(--ff-secondary);
      font-size: var(--fs-300);
      font-weight: 600;
    }
  }
}

